.search {
    width: 50%;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    text-align: center;
}

.search h2 {
    color: white;
    text-shadow: 0px 2px 10px rgba(0,0,0,0.5);
    margin: 30px auto 10px auto;
    font-size: 30px;
    width: 80%;
}

.search__input {
    margin: 10px auto;
    border-radius: 15px;
    border: none;
    -webkit-box-shadow: inset 0px 0px 10px rgba(0,0,0,0.3);
            box-shadow: inset 0px 0px 10px rgba(0,0,0,0.3);
    background-color: rgba(0,0,0,0.3);
    color: white;
    width: 80%;
    font-size: 16px;
    padding: 12px 20px;
}

.resultados {
    background-color: white;
    border-radius: 15px;
    width: 80%;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.resultados ul{
    display: inline-block;
    width: 100%;
    
    margin: 0;
    padding: 0
}

.resultados ul li {
    display: inline-block;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
    max-width: 100%;
    padding: 10px 20px;
}

.resultados ul a {
    display: -ms-flexbox;
    display: flex;
    text-decoration: none;
}

.resultados ul a .datos {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    text-align: left;
}

.resultados ul a .datos .texto {
    width: 60%;
    padding-left: 10%;
}

.resultados ul a .datos .precio {
    width: 30%;
    text-align: center;
    font-size: 25px;
}

.precio h5 {
    margin:20px 0;
}

.texto h3, .texto h4 {
    margin: 0;
    margin-top: 10px;
}

.resultados img {
    width: 80px;
    height: 80px;
}

.search button {
    font-family: 'Raleway', sans-serif;
    padding: 7px 30px;
    margin: 10px;
    margin-bottom: 20px;
    font-weight: bold;
    background-color: transparent;
    border-radius: 15px;
    color: rgba(255,255,255,0.4);
    border: 2px solid rgba(255,255,255,0.4);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.search button:hover {
    cursor: pointer;
    color: black;
    border:2px solid rgb(249, 242, 55);
    -webkit-box-shadow: 0px 0px 10px rgb(249, 242, 55);
            box-shadow: 0px 0px 10px rgb(249, 242, 55);
    background-color: rgba(249, 242, 55, 0.8);
}

@media screen and (max-width: 414px){
    .search {
        width: 95%;
    }
    .search h2 {
        font-size: 20px;
    }
    .resultados ul a .datos {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .resultados ul a .datos .precio {
        width: 100%;
    }
}

@media screen and (min-width: 415px) and (max-width: 800px){
    .search {
        width: 70%;
    }
    .search h2 {
        font-size: 22px;
    }
}


